	height: 120px;
	background: #6caddc;
	overflow: hidden;
	padding: 10px;
	text-align: center;
}

.card:hover {
	-webkit-transform: scale(1.06);
	    -ms-transform: scale(1.06);
	        transform: scale(1.06);
}

img {
	width: 60%;
}


